.trainingField {
  width: 100%;
  text-align: center;
}

.trainingField h2 {
  margin-top: 10px;
  margin-bottom: 20px;
}

.verb {
  text-align: left;
}

.atranslate {
  flex-basis: 60%;
}

.group {
  text-align: left;
}

.deleteBtn {
  background-color: rgb(125, 116, 177);
  color: #fff;
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 400;
  position: relative;
  top: -25px;
  border: none;
  align-self: flex-start;
}

.button {
  background-color: rgb(252, 87, 101);
  padding: 10px 20px;
  border-radius: 15px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.btnWrapper {
  text-align: left;
}

.btn {
  background-color: rgb(252, 87, 101);
  padding: 5px 10px;
  border-radius: 15px;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  border: none;
  cursor: pointer;
}

.translate {
  display: flex;
  justify-content: space-between;
}

.group {
  padding-top: 20px;
  padding-bottom: 20px;

}

.parentBox {
  position: relative;
  z-index: 10;
}

.qtitle {
  align-self: center;
  font-size: 16px;
  font-weight: 600;
  color: rgb(24, 9, 110);
  ;
}

.correct {
  color: rgb(91, 209, 138);
}

.wrong {
  color: rgb(252, 87, 101)
}

.checkBtn {
  cursor: pointer;
  padding: 10px 20px;
  background-color: rgb(125, 116, 177);
  border: none;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  color: #fff;
}