.select {
  position: relative;
  cursor: pointer;
  box-sizing: border-box;
  width: 150px;
  padding: 10px 20px;
  font-weight: 600;
  transition: all 0.3s;
}

.options {
  position: absolute;
  z-index: 10;
  width: 150px;
  box-sizing: border-box;
  border: 1px solid rgb(125, 116, 177);
  top: 43px;
  left: 0;
  transition: all 0.3s;
  cursor: pointer;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 0 5px #aaa;
  transition: all 0.3s;

}

.option {
  background-color: #fff;
  padding: 10px 20px;
  border-bottom: 2px solid rgb(125, 116, 177, 0.5);
  transition: all 0.5s;
}

.option:hover {
  background-color: rgba(186, 176, 240);
}