.nav {
  position: relative;
  width: 30%;
}

.navLinks {
  position: sticky;
  top: 100px;
  left: 0;
  margin: 0;
  padding: 20px;
  list-style: none;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 5px #aaa;
}

.link {
  padding: 10px 0;
  border-bottom: 1px solid #aaa;
}

.link:last-child {
  border: none;
}

.link a {
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  color: rgb(125, 116, 177);
}