.new_fill_form {
  margin-top: 100px;
}

.header {
  margin-top: 0;
}

.label {
  display: block;
}

.btn,
.btn_sub {
  background-color: transparent;
  border: 1px solid rgb(56, 56, 56);
  padding: 5px 10px;
  border-radius: 15px;
  color: rgb(56, 56, 56);
  transition: cubic-bezier(0.19, 1, 0.22, 1) 400ms;
  font-size: 14px;
}

.btns_wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.btn_sub {
  width: 100px;
  background-color: rgb(252, 87, 101);
  color: #fff;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.btn:hover {
  background-color: #aaa;
  color: #fff;
  border: 1px solid #aaa;
}

.btn_sub:hover {
  background-color: rgb(255, 127, 138);
}

.btn:disabled {
  color: #aaa;
  background-color: rgb(212, 212, 212);
  border: 1px solid #aaa;
}

.success img {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.success {
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  color: #27b24a;
  margin-bottom: 15px;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1);

}