.loading {
  margin: 0 auto;
  margin-top: 100px;
  width: 70px;
  height: 70px;
  animation: rotate 3000ms infinite linear;
}

.list {
  margin-top: 100px;
  width: 100%;
  height: 100vh;
}

.text {
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 600;
  color: #aaa;
  text-align: center;
}

.loading img {
  width: 70px;
  height: 70px;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}