.loginWrapper {
  display: flex;
  width: 500px;
  margin: auto;
  height: 100vh;
  align-items: center;
}

.loginError {
  border: 2px solid red;
}

.btn_wrapper {
  margin-top: 10px;
  display: flex;
  justify-content: flex-end;
}

form label {
  display: inline-block;
  box-sizing: border-box;
  padding-top: 10px;
}

.btn {
  padding: 5px 10px;
  cursor: pointer;
  min-width: 80px;
  border: 1px solid #aaa;
  border-radius: 40px;
  transition: cubic-bezier(0.19, 1, 0.22, 1) 400ms;
}

.register_toggle {
  position: absolute;
  bottom: 20px;
  left: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.register_toggle:hover {
  color: #aaa;
}

.errorNotice {
  color: rgb(248, 93, 93)
}