.trainingField {
  width: 100%;
  text-align: center;
}

.trainingField h2 {
  margin-top: 10px;
  margin-bottom: 20px;
}

.verb {
  text-align: left;
}

.atranslate {
  padding-top: 20px;
  padding-bottom: 20px;
  flex-basis: 60%;
}

.atranslate input {
  outline: none;
  border: none;
  border-radius: 0;
  border-bottom: 2px solid rgb(125, 116, 177);
  ;
  transition: all 0.3s;
}

.atranslate input:focus {
  border-bottom: 2px solid rgb(24, 9, 110);
}

.group {
  text-align: left;
}

.deleteBtn {
  background-color: rgb(248, 113, 124);
  color: #fff;
  display: flex;
  align-items: center;
  width: 20px;
  height: 20px;
  cursor: pointer;
  padding: 7px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 400;
  border: none;
  transition: all 0.3s;
}

.deleteBtn:hover {
  background-color: rgb(252, 87, 101);
}

.button {
  background-color: rgb(125, 116, 177);
  padding: 10px 20px;
  border-radius: 15px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  border: none;
  cursor: pointer;

}



.btnWrapper {
  text-align: left;
}

.btn {
  background-color: rgb(125, 116, 177);
  padding: 5px 10px;
  border-radius: 15px;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}



.translate {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.group {
  padding-top: 20px;
  padding-bottom: 20px;

}

.parentBox {
  position: relative;
  z-index: 10;
}

.qtitle {
  align-self: center;
  font-size: 16px;
  font-weight: 600;
  color: rgb(24, 9, 110);
  ;
}

.checkBtn {
  cursor: pointer;
  padding: 10px 20px;
  background-color: rgb(125, 116, 177);
  border: none;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  color: #fff;
  transition: 0.2s;
}

.btn:hover,
.checkBtn:hover {
  background-color: rgb(152, 140, 217)
}