.progress {
  display: flex;
  align-items: center;
}

.progressLine {
  flex-basis: 80%;
}

.number {
  flex-basis: 20%;
}

.progressLine {
  height: 20px;
  overflow: hidden;
  border-radius: 10px;
}

.greenLine {
  background-color: rgb(63, 245, 124);
  border-radius: 10px;
  height: 100%;
  transition: all 0.3s ease-in;
}