.header {
  display: flex;
  background-color: rgb(231, 231, 231);
  z-index: 2;
  width: 100%;
  justify-content: center;
  position: fixed;
  top: 0;
  padding-bottom: 20px;
}

.header ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  margin-top: 10px;
}

.header ul li {
  margin-left: 15px;
}

.header ul a {
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  color: rgb(125, 116, 177);
}