.card {
  border-radius: 20px;
  padding: 20px;
  margin: 20px;
  background-color: #fff;
  position: relative;
  box-shadow: 0 0 5px #aaa;
  transition: 0.3s linear;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

@keyframes bounce {
  0% {
    left: 10px;
  }

  20% {
    left: 0;
    right: 10px
  }

  40% {
    right: 0;
    left: 5px;
  }

  60% {
    left: 0;
    right: 5px;
  }

  80% {
    right: 0;
    left: 3px;
  }

  100% {
    right: 0;
  }
}

@keyframes rotate {
  0% {
    transform: rotateY(0);
  }

  50% {
    transform: rotateY(90deg);
  }

  100% {
    transform: rotateY(180deg);
  }
}

@keyframes back {
  0% {
    transform: rotateY(180deg);
  }

  50% {
    transform: rotateY(90deg);
  }

  100% {
    transform: rotateY(0);
  }
}

@keyframes contentOpacity {
  0% {
    opacity: 1;
  }

  5% {
    opacity: 0.25;
  }

  50% {
    opacity: 0;
  }

  95% {
    opacity: 0.88;
  }

  100% {
    opacity: 1;
  }
}

.error {
  border: 2px solid rgb(248, 93, 93);
  box-shadow: 0 0 5px rgb(255, 118, 118);
  animation: bounce 0.3s linear;
}

.flip {
  animation: rotate 0.5s ease-in;

}

.contentAnimation {
  animation: contentOpacity 0.5s ease-in
}


.notFlip {
  animation: back 0.5s ease-in;
}