.qtitle {
  align-self: center;
  font-size: 16px;
  font-weight: 600;
  color: rgb(24, 9, 110);
  ;
}

.qField {
  flex-basis: 60%;
  padding-top: 20px;
  padding-bottom: 20px;
}

.questions {
  display: flex;
  text-align: center;
  justify-content: space-between;
}

.btnWrapper {
  margin-top: 20px;
  text-align: right;
}

.btn {
  background-color: rgb(125, 116, 177);
  padding: 5px 10px;
  border-radius: 15px;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}

.btn:hover {
  background-color: rgb(152, 140, 217)
}