.card_appl {
  padding-top: 5px;
  padding-bottom: 5px;
  border-bottom: 1px solid rgb(204, 204, 204)
}

.card_appl:last-child {
  border: none;
}

.examples {
  display: flex;
}

.cart_examples li,
.cart_questions li {
  padding-top: 10px;
}

.cart_examples li:first-child,
.cart_questions li:first-child {
  padding-top: 0;
}

.cart_examples {
  list-style: none;
  flex-basis: 70%;
}

.cart_questions {
  flex-basis: 30%;
}

.card_fill {
  width: 70%;
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 10px;
}

.cart_translate {
  flex-basis: 30%;
  font-weight: 600;
  text-align-last: left;
  font-size: 18px;
  color: rgb(105, 104, 104);
  align-self: center;

}

.btn_sub {
  width: 100px;
  background-color: rgb(252, 87, 101);
  color: #fff;
  font-weight: 600;
  border: none;
  cursor: pointer;
  position: absolute;
  right: 20px;
  bottom: 7px;
  transition: cubic-bezier(0.19, 1, 0.22, 1) 400ms;
}

.btn_sub:hover {
  background-color: rgb(248, 41, 58);
}

.delete_btn,
.edit_btn,
.train_btn {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  right: 20px;
  top: 20px;
  cursor: pointer;
  border: 1px solid #aaa;
  border-radius: 40px;
  transition: cubic-bezier(0.19, 1, 0.22, 1) 400ms;
}

.train_btn__added {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  right: 20px;
  top: 20px;
  cursor: pointer;
  border: 2px solid rgb(28, 192, 107);
  box-shadow: 0 0 5px rgb(28, 192, 107);
  border-radius: 40px;
  transition: cubic-bezier(0.19, 1, 0.22, 1) 400ms;
}

.delete_btn:hover,
.edit_btn:hover,
.train_btn:hover,
.train_btn__added:hover {
  background-color: rgb(214, 214, 214);
}

.delete_btn img,
.train_btn img,
.train_btn__added img,
.edit_btn img {
  width: 15px;
  height: 15px;

}

.edit_btn {
  right: 60px;
}

.edit_btn img {
  width: 15px;
  height: 15px;
}