body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: rgb(231, 231, 231);
}

.container {
  max-width: 960px;
  margin: 0 auto;
}

.parent {
  position: relative;
}

.box-1,
.box-2 {
  position: relative;
  backface-visibility: 'hidden';
  top: 0;
  left: 0;
  width: 100%;
}

input {
  box-sizing: border-box;
  width: 100%;
  font-size: 14px;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  line-height: 18px;
  border-radius: 10px;
  border: 1px solid #aaa;
}