.load {
  animation: rotate 1s linear infinite;
  transform-origin: center;
}

.load img {
  width: 15px;
  height: 15px;
}

@keyframes rotate {
  0% {
    transform-origin: center;
    transform: rotate(0deg);
  }

  100% {
    transform-origin: center;
    transform: rotate(360deg);
  }
}