.delete {
  width: 30px;
  height: 30px;
  background-color: rgb(223, 74, 74);
  align-self: flex-end;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: cubic-bezier(0.19, 1, 0.22, 1) 400ms;
}

.delete:hover {
  background-color: rgb(143, 2, 2);
}

.delete_sub {
  width: 15px;
  height: 4px;
  background-color: #fff;

}