.label {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.label input {
  display: none;
}

.chekBoxWrapper {
  position: relative;
  display: flex;
  cursor: pointer;
  padding-left: 2px;
  padding-right: 2px;
  align-items: center;
  width: 40px;
  height: 25px;
  border-radius: 15px;
  box-shadow: 0 0 5px #aaa;
  transition: all 0.2s;
  background-color: rgb(247, 85, 85);
}

.circle {
  position: relative;
  width: 22px;
  height: 22px;
  border-radius: 15px;
  box-shadow: 0 0 4px rgb(128, 128, 128);
  transition: all 0.2s;
  background-color: #fff;
}

input:checked+.chekBoxWrapper .circle {
  transform: translateX(80%);

}

input:checked+.chekBoxWrapper {
  background-color: rgb(85, 247, 147);
}